import React from "react";

import { IServicePreview } from "../../types/services.type";

const ServicePreview = ({
  service,
  className,
}: {
  service: IServicePreview;
  className?: string;
}) => {
  const {
    html,
    frontmatter: {
      title,
      image: { publicURL: imageURL },
    },
  } = service;

  return (
    <div
      className={`service-preview break-inside-avoid flex flex-col h-min rounded-2xl p-10 mx-2 my-10 first:mt-0 md:mx-3 border-[3px] border-dashed border-main-100 text-secondary-100 cursor-pointer hover:invert transition ${className}`}
      itemScope
      itemType="https://schema.org/Service"
    >
      <div className="flex items-center">
        <img src={imageURL} alt={`${title} logo`} className="mr-7 invert" />
        <h4 className="font-extrabold text-2xl" itemProp="serviceType">
          {title}
        </h4>
      </div>
      <div
        className={`text-xl`}
        dangerouslySetInnerHTML={{ __html: html }}
        itemProp="description"
      />
    </div>
  );
};

export default ServicePreview;
