import React from "react";

import { HeadFC, useStaticQuery } from "gatsby";
import { graphql } from "gatsby";

import { IServicePreview } from "../types/services.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import ServicePreview from "../components/services/servicePreview";
import StackSection from "../components/stack/stackSection";

const Services = () => {
  const servicesData = useStaticQuery(query);
  let services = (
    servicesData?.allMarkdownRemark?.nodes?.length
      ? servicesData.allMarkdownRemark.nodes
      : null
  ) as IServicePreview[] | null;

  return (
    <>
      <Banner
        heightClass="h-136"
        title="our expertise is your advantage"
        text=""
      />
      <StackSection />
      <div className="bg-main-100">
        <ContentContainer className="flex-col items-center p-6 md:p-12">
          <div className="columns-1 md:columns-2 w-full py-14">
            {services
              ? services.map((service) => (
                  <ServicePreview service={service} key={service.id} />
                ))
              : null}
          </div>
        </ContentContainer>
      </div>
    </>
  );
};

export default Services;

export const Head: HeadFC = () => <title>CodeCave Services</title>;

const query = graphql`
  query {
    allMarkdownRemark(filter: { fields: { sourceName: { eq: "services" } } }) {
      nodes {
        id
        html
        frontmatter {
          title
          image {
            publicURL
          }
        }
      }
    }
  }
`;
